#root {
  text-align: center;
}

.margin-left {
  margin-left: 10px;
}

.header {
  background-color: rgb(0, 116, 228);
  color: white;
  padding: 5px;
}

.footer {
  padding: 20px 10px;
  font-size: 12px;
}

.link,
.link:visited,
.link:focus,
.link:hover {
  background-color: rgb(0, 116, 228);
  border-radius: 5px;
  color: white;
  display: inline-block;
  margin-top: 10px;
  padding: 10px;
  text-decoration: none;
}

.map-container {
  -ms-flex: 1 1;
      flex: 1 1;
  width: 400px;
}

.example {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 400px;
}

.code {
  margin: 0 0 0 10px !important;
  -ms-flex: 1 1;
      flex: 1 1;
  height: 100%;
}



